.screen_contact {
    display: grid;
    grid-template-areas: "top top" "left right";
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto auto;
    max-width: 800px;
    margin: 0 auto;
    padding: 15px;
    gap: 15px;
    align-content: center;

    @media (max-width: 600px) {
        grid-template-columns: 1fr;
        grid-template-rows: auto auto auto;
        grid-template-areas: "top" "left" "right";
        align-content: start;
    }
}

.contact-col {
    &__avatar-container {
        flex: 0 0 120px;
    }

    &__info-container {
        flex: 1 1 300px;
    }

    &__avatar {
        height: 100px;
        width: 100px;
        border-radius: 50%;
        margin: 0 20px 20px 0;
    }

    &__name {
        font-size: 27px;
        text-transform: uppercase;
        line-height: 1;
        margin-top: -4px;
        margin-bottom: 10px;
    }

    &__position,
    &__location {
        color: var(--color-text-secondary);
    }

    &__interests {
        // font-size: 20px;
        margin-top: 10px;
    }

    &__title {
        color: var(--color-text-secondary);
        font-weight: bold;
        text-transform: uppercase;
    }

    &_top {
        display: flex;
        @media (max-width: 600px) {
            flex-wrap: wrap;
        }

        grid-area: top;
        margin-bottom: 20px;
    }
    &_left {
        grid-area: left;
    }
    &_right {
        grid-area: right;
    }
}
