.card {
    width: 242px;
    height: max-content;
    position: relative;

    border: solid 1px var(--color-primary-02);
    border-radius: 3px;

    &__background {
        background-color: var(--color-background-03);
        backdrop-filter: blur(5px);
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        z-index: -1;
    }

    &__image-wrapper {
        display: block;
        text-decoration: none;
    }
    &__image {
        height: 150px;

        padding: 10px 30px;

        img {
            object-fit: scale-down;
            object-position: center;
            width: 100%;
            height: 100%;
        }
    }

    &__image-placeholder {
        display: flex;
        align-items: center;
        justify-content: center;
        line-height: 1.2;

        height: 100%;

        font-weight: bold;
        font-size: 28px;
        text-align: center;
        text-decoration: none !important;
        text-transform: uppercase;
    }

    &__content {
        overflow: auto;

        padding: 10px;

        font-size: 14px;
    }

    &__header {
        color: var(--color-text-primary);
        font-size: 16px;
    }

    &__meta {
        color: var(--color-text-secondary);
        font-size: 12px;
    }

    &__paragraph {
        margin-top: 10px;
    }

    &__extra-content {
        padding: 10px;
        border-top: var(--color-primary-02) 1px solid;

        color: var(--color-text-secondary);
        font-size: 12px;
    }
}
