:root {
    --color-primary: #766b6b;
    --color-primary-02: rgba(118, 107, 107, 0.2);
    // --color-primary-02: hsla(100, 75%, 47%, 0.2);

    @media (prefers-color-scheme: dark) {
        --color-background: #000;
        --color-background-03: rgba(0, 0, 0, 0.3);
        --color-text-primary: #c8c8c8;
        --color-text-secondary: #8d9aa2;
        --color-link: #628fc8;
    }

    @media (prefers-color-scheme: light) {
        --color-background: #fff;
        --color-background-03: rgba(255, 255, 255, 0.3);
        --color-text-primary: #0e0e0e;
        --color-text-secondary: #766b6b;
        --color-link: #094b9b;
    }
}
